.Posts {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    width: 80%;
    margin: auto;
}
.Posts ul{
    list-style:none;
    margin:0;
    padding:0;
}
.Posts li{
    display:inline-block;
    margin:20px;
}
.Posts a{
    text-decoration: none;
    color: black;
    margin:20px

}
.Posts a:hover,
.Posts a:active,
.Posts a.active{
    color: salmon
}